
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'custom-dialog',
  props: {
    show: {
      type: Boolean, 
      required: false,
      default: false
    },
    closeButton: {
      type: Boolean, 
      required: false,
      default: true
    }
  },
  methods: {
    hide(): void {
      this.$emit('hideDialog')
    }
  }
})
